body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  position: relative;
}


.quiz-wrapper{
    background: #8989ad;
}
body{
    background: #8989ad;
}
.sorting-hat{
    margin: 20px 0px;
}
.quiz-question{
    max-width:600px;
    margin:0 auto;
    font-size:1.4em;
}
.quiz-answers{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding:30px;
}
.quiz-answer{
    background-color: #fff;
    text-align: left;
    margin: 20px 5%;
    border-radius: 10px;
    width:400px;
    max-width:100%;
    font-family: Noto Sans,sans-serif;
    box-shadow: 0 1px 2px rgba(0,0,0,.1);
    -webkit-transition: all .6s cubic-bezier(.165,.84,.44,1);
    transition: all .6s cubic-bezier(.165,.84,.44,1);
    text-align: center;
    padding:20px;
    cursor: pointer;
}

.quiz-answer:hover{
    -webkit-transform: scale(1.05, 1.05);
            transform: scale(1.05, 1.05);
    background:#2c2c3ecf;
    color:white;
}
.house-result{
    background: #ffffff7a;
    width: 300px;
    margin: auto;
    padding: 20px;
    border-radius: 20px;
}

.quiz-start{
    width: 100%;
    height:100%;
    margin:0;
    background: #bbbbc7ba;
    position: absolute;
}
.message-content{
    position: relative;
    max-width:400px;
    max-height: 100%;
    margin: auto;
    padding:10px;
}
.close-button{
    position: absolute;
    right:0;
}
.message-wrapper{
    padding:25px;
    border-radius:10px;
    background:white;
    max-width:400px;
    margin: 5% auto;
    font-size:24px;
}
.start-quiz{
    font-size:20px;
    width:150px;
    background: purple;
    color:white;
    padding:15px;
    border-radius: 10px;
}
